import React, { Suspense, useRef } from 'react';
import { useEffect, useState } from 'react';
import { useWeb3React } from '@web3-react/core';
import './style.scss';
import '../../styles/main.scss';
import {
  ArrowUpIcon,
  DoubleRightArrowIcon,
  FacebookLogoIcon,
  LanguageIcon,
  LeftArrowIcon,
  LzLogoIcon,
  MenuIcon,
  TelegramIcon,
  ThemeIcon,
  TwitterIcon,
  XIcon,
  DoubleLeftArrowIcon,
  LeftArrowGreyIcon,
  DownArrowGreyIcon,
  CountryFlagIcon,
  PadIcon, DocsIcon, HelpIcon, GlobalCommunityLogoIcon
} from '../Icons';
import 'web3-react-modal/dist/index.css';
import { Web3ReactModal } from 'web3-react-modal';
import connectors from '../../utils/connectors';
import { weiToNumber } from '../../utils/helpers';
import {Link, Route, useLocation, matchPath, useHistory} from 'react-router-dom';
import { Parallax, ParallaxLayer, IParallax } from '@react-spring/parallax';
import _ from 'lodash';
import { LzButton } from '../LzButtons';
import { AccountInfoButton } from '../AccountInfoButton';
import { LzText } from '../LzText';
import { CopyableAddress } from '../CopyableAddress';
import LzAbi from '../../assets/abis/erc20.json';
import { ethers } from 'ethers';
import PerfectScrollbar from 'react-perfect-scrollbar';
import 'react-perfect-scrollbar/dist/css/styles.css';
import { BrowserView, MobileView } from '../DeviceDetect';
import OutsideClickHandler from 'react-outside-click-handler';
import { useHotkeys } from 'react-hotkeys-hook';
import { useLanguage } from "../../hooks/use-language";
import GitInfo from 'react-git-info/macro';

const CrossStorageClient = require('cross-storage').CrossStorageClient

const LS_CONNECTOR = 'web3connector';
const LS_THEME = 'theme';
const DEFAULT_APP_PATH = '/swap';

type DappType = {
  configs: {
    icon: (props: React.HTMLAttributes<HTMLImageElement>) => JSX.Element;
    name: string;
    path: string;
    menuLink?: string;
    children?: {
      name: string;
      path: string;
      menuLink?: string;
    }[];
  };
  Component: any;
};

type SubMenuItem = { name: string; path: string };
type MenuItem = SubMenuItem & { children: MenuItem[]; icon: React.Component };

export const AppLayout = (props: { dapps: DappType[] }) => {
  const { activate, account, deactivate, chainId, library } = useWeb3React();
  const [bnbBalance, setBnbBalance] = useState<any>();
  const [lzBalance, setLzBalance] = useState<any>();
  const [visibleWalletModal, setVisibleWalletModal] = useState<any>();
  const [theme, setTheme] = useState<'dark' | 'light'>('light');
  const dapps = props.dapps;
  const location = useLocation();
  const history = useHistory();
  const [showMobileFooterExpand, setShowMobileFooterExpand] = useState<boolean>(false);
  const [showMobileMenu, setShowMobileMenu] = useState<boolean>(false);
  const [showDesktopWalletExpand, setShowDesktopWalletExpand] = useState<boolean>(false);
  const [showSelectLanguage, setShowSelectLanguage] = useState<boolean>(false)
  const { language, setLanguage, getText, supportedLanguages } = useLanguage()
  const refAccountBtn = useRef(null);
  const refMobileAccountBtn = useRef(null);
  const [xStorageClient, setXStorageClient] = useState<typeof CrossStorageClient>(undefined)

  useEffect(() => {
    // dapps.map((d: any) =>d.configs).find((config: any) => config.path === location.pathname)

    for(let i in dapps) {
      // if child menu is match with path
      const children = dapps[i].configs.children;
      for (let j in children) {
        if (matchPath(location.pathname, { path: children[j].path, exact: children[j].path === '/' })) {
          document.title = 'LaunchZone | ' + children[j].name
          return
        }
      }

      if (matchPath(location.pathname, {path: dapps[i].configs.path})) {
        document.title = 'LaunchZone | ' + (dapps[i].configs.name === 'Home' ? 'The Ultimate DeFi Platform' : dapps[i].configs.name)
      }
    }

  }, [location.pathname]);

  useEffect(() => {
    if(location.pathname === '/') {
      history.push(DEFAULT_APP_PATH)
    }
  }, [location.pathname])

  useHotkeys('esc', (e) => {
    e.preventDefault();
    setShowMobileFooterExpand(false);
    setShowDesktopWalletExpand(false);
  });

  useEffect(() => {
    if (!process.env.REACT_APP_X_STORAGE_URL) {
      return
    }
    const storage = new CrossStorageClient(process.env.REACT_APP_X_STORAGE_URL)
    storage
      .onConnect()
      .then(() => {
        setXStorageClient(storage)
      })
      .then(() => {
        console.log('x-storage', 'connected', process.env.REACT_APP_X_STORAGE_URL)
        const refAddress = (new URLSearchParams(location.search)).get('r')
        if (refAddress) {
          const formalizedAddress = ethers.utils.getAddress(refAddress)
          return storage.set('LZ_REFERRAL', formalizedAddress)
            .then(() => console.log('x-storage', 'LZ_REFERRAL', formalizedAddress))
        }
      })
      .catch(console.error)
  }, [process.env.REACT_APP_X_STORAGE_URL])

  useEffect(() => {
    const initTheme = localStorage.getItem(LS_THEME) as 'dark' | 'light';
    setTheme(initTheme ? initTheme : 'light');

    const gitInfo = GitInfo();
    console.log('Environment:', process.env.REACT_APP_NODE_ENV)
    console.log('Hash:', gitInfo.commit.hash)
  }, []);

  useEffect(() => {
    const initConnector = localStorage.getItem(LS_CONNECTOR);
    if (initConnector) {
      const connector = Object.values(connectors)
        .map(({ connector }) => connector)
        .find((connector) => connector?.constructor?.name === initConnector);
      if (connector) {
        activate(connector);
      }
    }
  }, [activate]);

  useEffect(() => {
    if (!!account && !!library) {
      library
        .getBalance(account)
        .then((balance: any) => {
          setBnbBalance(weiToNumber(balance));
        })
        .catch(() => {
          setBnbBalance(null);
        });
      setBnbBalance(undefined);
    }
  }, [account, library, chainId]);

  useEffect(() => {
    if (!!account && !!library) {
      trackConnectedAccount(account)
      const contract = new ethers.Contract(
        '0x3b78458981eb7260d1f781cb8be2caac7027dbe2',
        LzAbi,
        library,
      );
      contract
        .balanceOf(account)
        .then((lzBalance: any) => {
          setLzBalance(weiToNumber(lzBalance));
        })
        .catch(() => {
          setLzBalance(null);
        });
      setLzBalance(undefined);
    }
  }, [account, library, chainId]);

  const parallax = useRef<IParallax>(null);

  const trackConnectedAccount = (acc: string) => {
    fetch(process.env.REACT_APP_TRACK_ACC_API + '/api/detail/' + acc).catch((e) => console.error(e))
  }

  const findSelectedMenuItem = () => {
    for(let i in dapps) {
      const children = dapps[i].configs.children;

      for(let j in children) {
        if(matchPath(location.pathname, { path: children[j].path, exact: children[j].path === '/' })) {
          return {
            Component: dapps[i].Component[j],
            configs: children[j]
          }
        }
      }

      if(matchPath(location.pathname, { path: dapps[i].configs.path, exact: dapps[i].configs.path === '/' })) {
        return dapps[i];
      }
    }

    return dapps[0];
  }

  const isMenuExpand = ({configs}: any) => {
    if(matchPath(location.pathname, { path: configs.path, exact: configs.path === '/' })) {
      return true;
    } else if(configs.children) {
      for (let child of configs.children) {
        if(matchPath(location.pathname, { path: child.path, exact: child.path === '/' })) {
          return true;
        }
      }
    }
    return false;
  }

  const selectedMenuItem = findSelectedMenuItem();
  const Component = selectedMenuItem?.Component;

  return (
    <>
      <div className={`${theme ? theme : 'light'}`}>
        <div className='body'>
          <BrowserView>
            <PerfectScrollbar className='sidebar'>
              <div className='sidebar__top'>
                <Link to={DEFAULT_APP_PATH} className='sidebar__lz-logo-container'>
                  <LzLogoIcon />
                </Link>
                <div className='sidebar__menu-item-list'>
                  {dapps?.map(({ configs: menuItem }) => {
                    const Icon = menuItem.icon;
                    const children = menuItem.children
                    return (
                      <div className='sidebar__menu-item'>

                        <Link to={menuItem.menuLink || menuItem.path}>
                          <div className={`sidebar__menu-item__label ${isMenuExpand({configs: menuItem}) && 'active'}`}
                          >
                            {!!menuItem.icon ? <Icon className='sidebar__menu-item__icon' /> : null}
                            <div className='sidebar__menu-item__title'>
                              <span className='sidebar__menu-item__title-text'>
                                {menuItem.name}
                              </span>
                            </div>
                            <div className={`sidebar__menu-item__title-selected ${isMenuExpand({configs: menuItem}) || matchPath(location.pathname, {
                              path: menuItem.path,
                              exact: menuItem.path === '/',
                            }) ? 'active' : ''}`}
                            />
                          </div>
                        </Link>
                        <div className={`sidebar__menu-item__submenu ${isMenuExpand({configs: menuItem}) ? 'expand': ''}`}>
                          {children?.map((subMenuItem) => {
                            if (
                              matchPath(location.pathname, {
                                path: subMenuItem.path,
                                exact: subMenuItem.path === '/',
                              })
                            ) {
                              return (
                                <Link to={menuItem.menuLink || menuItem.path}>
                                  <LzText className='sidebar__sub-menu-item'>
                                    {subMenuItem.name}
                                  </LzText>
                                </Link>
                              );
                            }
                            return (
                              <Link to={subMenuItem.menuLink || subMenuItem.path}>
                                <div className='sidebar__sub-menu-item'>
                                  {subMenuItem.name}
                                </div>
                              </Link>
                            );
                          })}
                        </div>
                      </div>
                    );
                  })}
                  <div className='sidebar__menu-item'>
                    <a href="/pad">
                      <div className='sidebar__menu-item__label'>
                        <PadIcon className='sidebar__menu-item__icon' />
                        <div className='sidebar__menu-item__title'>
                          <span className='sidebar__menu-item__title-text'>
                            Pad
                          </span>
                        </div>
                        <div className='sidebar__menu-item__title-selected'
                        />
                      </div>
                    </a>
                  </div>
                  <div className='sidebar__menu-item'>
                    <a href="https://docs.lz.finance/" target='_blank'>
                      <div className='sidebar__menu-item__label'>
                        <DocsIcon className='sidebar__menu-item__icon' />
                        <div className='sidebar__menu-item__title'>
                          <span className='sidebar__menu-item__title-text'>
                            Docs
                          </span>
                        </div>
                        <div className='sidebar__menu-item__title-selected'
                        />
                      </div>
                    </a>
                  </div>
                  <div className='sidebar__menu-item'>
                    <a href="https://help.lz.finance/" target='_blank'>
                      <div className='sidebar__menu-item__label'>
                        <HelpIcon className='sidebar__menu-item__icon' />
                        <div className='sidebar__menu-item__title'>
                          <span className='sidebar__menu-item__title-text'>
                            Help
                          </span>
                        </div>
                        <div className='sidebar__menu-item__title-selected'
                        />
                      </div>
                    </a>
                  </div>
                </div>
                <div
                  ref={refAccountBtn}
                  className='sidebar__account-info-button-container'
                  onClick={() => {
                    account &&
                      setShowDesktopWalletExpand(
                        (showDesktopWalletExpand) => !showDesktopWalletExpand,
                      );
                    !account && setVisibleWalletModal(true);
                  }}
                >
                  {!account ? (
                    <LzButton theme='dark' className='sidebar__connect-wallet-button'>
                      {getText('connect_wallet')}
                    </LzButton>
                  ) : null}
                  {account ? <AccountInfoButton account={account} balance={bnbBalance} /> : null}
                </div>
              </div>
              <div className='sidebar__bottom'>
                <div className='sidebar__bottom__container'>
                  <div className='sidebar__bottom__configs'>
                    <ThemeIcon
                      className='sidebar__bottom__theme-icon'
                      onClick={() => {
                        const themeToSet = theme === 'dark' ? 'light' : 'dark';
                        localStorage.setItem(LS_THEME, themeToSet);
                        setTheme(themeToSet);
                      }}
                      type={theme}
                    />
                    <OutsideClickHandler
                      onOutsideClick={() => {
                        setShowSelectLanguage(false);
                      }}
                    >
                      <div className='sidebar__bottom__language' onClick={() => { setShowSelectLanguage(!showSelectLanguage) }}>
                        <LanguageIcon className='sidebar__bottom__language-icon' />
                        <span className='sidebar__bottom__language-text'>{language}</span>
                      </div>
                    </OutsideClickHandler>
                  </div>
                  <div className='sidebar__bottom__social-media-icons'>
                    <a href="https://t.me/lzofficial" target='_blank'>
                      <TelegramIcon />
                    </a>
                    <a href="https://t.me/launchzoneann" target='_blank'>
                      <GlobalCommunityLogoIcon />
                    </a>
                    <a href="https://twitter.com/launchzoneann" target='_blank'>
                      <TwitterIcon />
                    </a>
                    <a href="https://www.facebook.com/launchzoneann" target='_blank'>
                      <FacebookLogoIcon />
                    </a>
                  </div>
                </div>
              </div>
            </PerfectScrollbar>
            <div className={`sidebar__bottom__language--select ${showSelectLanguage ? 'active' : ''}`}>
              <span className='btn-close-language'>
                <LeftArrowGreyIcon />
              </span>
              {
                Object.entries(supportedLanguages).map((lang, key) => {
                  return (
                    <div
                      onClick={() => {
                        setLanguage(lang[0])
                      }
                      }
                      key={key}
                      className={`sidebar__bottom__language--select-option ${language === lang[0] ? 'selected' : ''}`}
                    >
                      <CountryFlagIcon country={lang[0]} />
                      <span className='ml-1'>{lang[1]}</span>
                    </div>
                  )
                })
              }
            </div>

            <div className='desktop-content'>
              {Component && (
                <Suspense fallback={null}>
                  <Component
                    language={language}
                    theme={theme}
                    useWeb3React={useWeb3React}
                    xStorageClient={xStorageClient}
                    useSubPage={() => location.pathname}
                    env={process.env.REACT_APP_NODE_ENV || 'production'}
                    showConnectWalletModal={() => setVisibleWalletModal(true)}
                  />
                </Suspense>
              )}
            </div>
            <OutsideClickHandler
              disabled={!showDesktopWalletExpand}
              onOutsideClick={(e: any) => {
                if (!e.path.includes(refAccountBtn.current)) {
                  setShowDesktopWalletExpand(false)
                }
              }}
            >
              <div className={`sidebar__expand ${showDesktopWalletExpand ? 'show' : ''}`}>
                <LeftArrowIcon
                  className='sidebar__expand__left-arrow-icon'
                  onClick={() => {
                    setShowDesktopWalletExpand(
                      (showDesktopWalletExpand) => !showDesktopWalletExpand,
                    );
                  }}
                />
                <LzText>{getText('your_address')}</LzText>
                <CopyableAddress>{account!}</CopyableAddress>
                <div className='sidebar__expand__bnb-balance'>
                  <div>{getText('bnb_balance')}</div>
                  <div>{bnbBalance}</div>
                </div>
                <div className='sidebar__expand__lz-balance'>
                  <div>{getText('lz_balance')}</div>
                  <div>{lzBalance}</div>
                </div>

                <LzText className='sidebar__expand__view-on-bsc-scan'>
                  <a target='_blank' href={`https://bscscan.com/address/${account}`}>
                    {getText('view_on_bscscan')}
                  </a>
                </LzText>
                <LzButton
                  onClick={() => {
                    deactivate();
                    localStorage.removeItem(LS_CONNECTOR);
                    setShowDesktopWalletExpand(false);
                  }}
                >
                  {getText('disconnect_wallet')}
                </LzButton>
              </div>
            </OutsideClickHandler>
          </BrowserView>
          <MobileView>
            <div className='navbar'>
              <div className='navbar__top-line'>
                <div className='navbar__lz-logo-icon-container'>
                  <LzLogoIcon />
                </div>
                <LzText className='navbar__title'>{selectedMenuItem?.configs.name}</LzText>
                {showMobileMenu ? (
                  <div className='navbar__menu-icon'>
                    <ArrowUpIcon
                      onClick={() => {
                        setShowMobileMenu(false);
                      }}
                    />
                  </div>
                ) : (
                  <div className='navbar__menu-icon'>
                    <MenuIcon
                      onClick={() => {
                        setShowMobileMenu(true);
                      }}
                    />
                  </div>
                )}
              </div>
              <div className={`navbar__menu-item-list ${showMobileMenu && 'active'} `}>
                {dapps?.map(({ configs: menuItem }) => {
                  const Icon = menuItem.icon;

                  if(menuItem.children && menuItem.children.length > 0) {
                    return menuItem.children.map((child: any) => (
                      <Link to={child.menuLink || child.path} className='navbar__menu-item'>
                        {!!child.icon ? <Icon className='navbar__menu-item__icon' /> : null}
                        <div className='navbar__menu-item__title'>
                          <span className='navbar__menu-item__title-text'>{child.name}</span>
                          {matchPath(location.pathname, {
                            path: child.path,
                            exact: child.path === '/',
                          }) ? (
                            <div className='navbar__menu-item__title-selected' />
                          ) : null}
                        </div>
                      </Link>
                    ))
                  }

                  return (
                    <Link to={menuItem.menuLink || menuItem.path} className='navbar__menu-item'>
                      {!!menuItem.icon ? <Icon className='navbar__menu-item__icon' /> : null}
                      <div className='navbar__menu-item__title'>
                        <span className='navbar__menu-item__title-text'>{menuItem.name}</span>
                        {matchPath(location.pathname, {
                          path: menuItem.path,
                          exact: menuItem.path === '/',
                        }) ? (
                          <div className='navbar__menu-item__title-selected' />
                        ) : null}
                      </div>
                    </Link>
                  );
                })}
                <a className='navbar__menu-item' href="/pad">
                  <PadIcon className='navbar__menu-item__icon' />
                  <div className='navbar__menu-item__title'>
                    <span className='navbar__menu-item__title-text'>
                      Pad
                    </span>
                  </div>
                </a>
                <a className='navbar__menu-item' href="https://docs.lz.finance/" target='_blank'>
                  <DocsIcon className='navbar__menu-item__icon' />
                  <div className='navbar__menu-item__title'>
                    <span className='navbar__menu-item__title-text'>
                      Docs
                    </span>
                  </div>
                </a>
                <a className='navbar__menu-item' href="https://help.lz.finance/" target='_blank'>
                  <HelpIcon className='navbar__menu-item__icon' />
                  <div className='navbar__menu-item__title'>
                    <span className='navbar__menu-item__title-text'>
                      Help
                    </span>
                  </div>
                </a>
              </div>
            </div>
            {selectedMenuItem?.configs.children?.length && showMobileMenu ? (
              <div className='navbar__sub-menu-item-list'>
                <Parallax
                  className='navbar__sub-menu-item-list__parallax'
                  ref={parallax}
                  pages={Math.ceil(selectedMenuItem?.configs.children?.length / 4)}
                  horizontal
                >
                  {_(selectedMenuItem?.configs.children)
                    .chunk(4)
                    .map((subMenuItemList, i) => {
                      const pageType = (() => {
                        if ((selectedMenuItem?.configs?.children?.length ?? 0) <= 4) {
                          return 'SINGLE';
                        }
                        if (i === 0) {
                          return 'FIRST';
                        }
                        if ((i + i) * 4 >= (selectedMenuItem?.configs?.children?.length ?? 0)) {
                          return 'LAST';
                        }
                        return 'MIDDLE';
                      })();
                      const gridTemplateColumns = {
                        SINGLE: '1fr 1fr 1fr 1fr',
                        FIRST: '1fr 1fr 1fr 1fr 0.4fr',
                        MIDDLE: '0.4fr 1fr 1fr 1fr 1fr 0.4fr',
                        LAST: '0.4fr 1fr 1fr 1fr 1fr',
                      }[pageType];
                      return (
                        <ParallaxLayer
                          className='navbar__sub-menu-item-list__parallax-layer'
                          offset={i}
                        >
                          <div
                            className='navbar__sub-menu-item-list__grid'
                            style={{
                              gridTemplateColumns,
                            }}
                          >
                            {['MIDDLE', 'LAST'].includes(pageType) ? (
                              <div
                                className='navbar__sub-menu-item-list__prev-icon'
                                onClick={() => {
                                  if (parallax.current) {
                                    parallax.current.scrollTo(i - 1);
                                  }
                                }}
                              >
                                <DoubleLeftArrowIcon />
                              </div>
                            ) : null}
                            {subMenuItemList.map((subMenuItem) => {
                              return (
                                <Link
                                  className='navbar__sub-menu-item-list__item-title'
                                  to={subMenuItem.menuLink || subMenuItem.path}
                                >
                                  {subMenuItem.name}
                                </Link>
                              );
                            })}
                            {['MIDDLE', 'FIRST'].includes(pageType) ? (
                              <div
                                className='navbar__sub-menu-item-list__next-icon'
                                onClick={() => {
                                  if (parallax.current) {
                                    parallax.current.scrollTo(i + 1);
                                  }
                                }}
                              >
                                <DoubleRightArrowIcon />
                              </div>
                            ) : null}
                          </div>
                        </ParallaxLayer>
                      );
                    })
                    .value()}
                </Parallax>
              </div>
            ) : null}
            <div className='mobile-content'>
              {Component && (
                <Suspense fallback={null}>
                  <Component
                    theme={theme}
                    language={language}
                    useWeb3React={useWeb3React}
                    useSubPage={() => location.pathname}
                    xStorageClient={xStorageClient}
                    env={process.env.REACT_APP_NODE_ENV || 'production'}
                    showConnectWalletModal={() => setVisibleWalletModal(true)}
                  />
                </Suspense>
              )}
            </div>
            <OutsideClickHandler
              disabled={!showMobileFooterExpand}
              onOutsideClick={(e: any) => {
                if (!e.path.includes(refMobileAccountBtn.current)) {
                  setShowMobileFooterExpand(false);
                }
              }}
            >
              <div className={`footer__expand ${showMobileFooterExpand ? 'show' : ''}`}>
                <LzText>{getText('your_address')}</LzText>
                <CopyableAddress>{account!}</CopyableAddress>
                <div className='footer__expand__bnb-balance'>
                  <div>{getText('bnb_balance')}</div>
                  <div>{bnbBalance}</div>
                </div>
                <div className='footer__expand__lz-balance'>
                  <div>{getText('lz_balance')}</div>
                  <div>{lzBalance}</div>
                </div>
                <div className='footer__expand__view-on-bsc-scan'>
                  <LzText className='footer__expand__view-on-bsc-scan-link'>
                    <a target='_blank' href={`https://bscscan.com/address/${account}`}>
                      {getText('view_on_bscscan')}
                    </a>
                  </LzText>
                  <div className='footer__expand__social-medias'>
                    <a href="https://t.me/lzofficial" target='_blank'>
                      <TelegramIcon />
                    </a>
                    <a href="https://t.me/launchzoneann" target='_blank'>
                      <GlobalCommunityLogoIcon />
                    </a>
                    <a href="https://twitter.com/launchzoneann" target='_blank'>
                      <TwitterIcon />
                    </a>
                    <a href="https://www.facebook.com/launchzoneann" target='_blank'>
                      <FacebookLogoIcon />
                    </a>
                  </div>
                </div>
                <LzButton
                  onClick={() => {
                    deactivate();
                    localStorage.removeItem(LS_CONNECTOR);
                    setShowMobileFooterExpand(false);
                  }}
                >
                  {getText('disconnect_wallet')}
                </LzButton>
              </div>
            </OutsideClickHandler>
            <div className='footer'>
              {!account ? (
                <LzButton
                  theme='dark'
                  className='footer__connect-wallet-button'
                  onClick={() => {
                    setVisibleWalletModal(true);
                  }}
                >
                  {getText('connect_wallet')}
                </LzButton>
              ) : null}
              {account ? (
                <div
                  ref={refMobileAccountBtn}
                >
                  <AccountInfoButton
                    onClick={() => {
                      console.log(showMobileFooterExpand)
                      setShowMobileFooterExpand(!showMobileFooterExpand);
                    }}
                    account={account}
                    balance={bnbBalance}
                  />
                </div>
              ) : null}
              <ThemeIcon
                type={theme}
                onClick={() => {
                  const themeToSet = theme === 'dark' ? 'light' : 'dark';
                  localStorage.setItem(LS_THEME, themeToSet);
                  setTheme(themeToSet);
                }}
              />
              <OutsideClickHandler
                onOutsideClick={() => {
                  setShowSelectLanguage(false);
                }}
              >
                <div className='footer__language' onClick={() => setShowSelectLanguage(!showSelectLanguage)}>
                  <LanguageIcon className='footer__language-icon' />
                  <span className='footer__language-text'>{language.toUpperCase()}</span>
                  <div className={`footer__language--select ${showSelectLanguage ? 'active' : ''}`}>
                    <span className='btn-close-language'>
                      <DownArrowGreyIcon />
                    </span>
                    {
                      Object.entries(supportedLanguages).map((lang, key) => {
                        return (
                          <div
                            onClick={() => {
                              setLanguage(lang[0])
                            }
                            }
                            key={key}
                            className={`footer__language--select-option ${language === lang[0] ? 'selected' : ''}`}
                          >
                            {lang[1]}
                          </div>
                        )
                      })
                    }
                  </div>
                </div>
              </OutsideClickHandler>
              {showMobileFooterExpand ? (
                <XIcon
                  onClick={() => {
                    setShowMobileFooterExpand((showMobileFooter) => !showMobileFooter);
                  }}
                />
              ) : (
                <MenuIcon
                  onClick={() => {
                    account && setShowMobileFooterExpand((showMobileFooter) => !showMobileFooter);
                    !account && setVisibleWalletModal(true);
                  }}
                />
              )}
            </div>
          </MobileView>
          <Web3ReactModal
            visible={visibleWalletModal}
            setVisible={setVisibleWalletModal}
            providerOptions={connectors}
            onConnect={(connector: any,_:any,email:string) => {
              const name = connector?.constructor?.name;
              if (email && name === 'MagicConnector'){
                connector.email = email
              }
              activate(connector, (err) => {
                alert(err.toString())
              });
              if (name) {
                localStorage.setItem(LS_CONNECTOR, name);
              }
            }}
          />
        </div>
      </div>
    </>
  );
};
