import React, {Suspense} from 'react';
import ReactDOM from 'react-dom';
import {Web3ReactProvider} from '@web3-react/core';
import {ethers} from 'ethers';
import {AppLayout} from './components/AppLayout';
import {BrowserRouter as Router} from 'react-router-dom';
import './styles/main.scss';
import './index.css';

function getLibrary(provider: any) {
  const library = new ethers.providers.Web3Provider(provider);
  return library;
}

const earnConfig = {
  icon: (props: any) => (
    <img src='/icons/earn.svg' {...props} />
  ),
  name: 'Earn',
  children: [
    require('sfarm-interface/dist/configs'),
    require('pool-interface/dist/configs')
  ],
  path: '/bank'
}

const exchangeConfig = {
  icon: (props: any) => (
    <img src='/icons/exchange.svg' {...props} />
  ),
  name: 'Exchange',
  children: [
    require('swap-interface/dist/configs')
  ],
  path: '/swap'
}

ReactDOM.render(
  <React.StrictMode>
    <Web3ReactProvider getLibrary={getLibrary}>
      <Router>
        <AppLayout
          dapps={[
            {
              Component: React.lazy(() => {
                // @ts-ignore
                import('home-interface/dist/component.css');
                return import('home-interface/dist/component');
              }),
              configs: require('home-interface/dist/configs'),
            },
            {
              Component: [
                React.lazy(() => {
                  // @ts-ignore
                  import('swap-interface/dist/component.css');
                  return import('swap-interface/dist/component');
                })
              ],
              configs: exchangeConfig,
            },
            {
              configs: earnConfig,
              Component: [
                React.lazy(() => {
                  // @ts-ignore
                  import('sfarm-interface/dist/component.css');
                  return import('sfarm-interface/dist/component');
                }),
                React.lazy(() => {
                  // @ts-ignore
                  import('pool-interface/dist/index.css');
                  return import('pool-interface/dist');
                })
              ]
            },
          ]}
        />
      </Router>
    </Web3ReactProvider>
  </React.StrictMode>,
  document.getElementById('root'),
);
