export const ThemeIcon = (props: SubIconProps & { type: 'light' | 'dark' }) => {
  if (props.type === 'light') {
    return (
      <BaseIcon {...props.containerProps}>
        <img src='/icons/theme.svg' alt='' {...props} />
      </BaseIcon>
    );
  }
  return (
    <BaseIcon {...props.containerProps}>
      <img src='/icons/dark-theme.svg' alt='' {...props} />
    </BaseIcon>
  );
};

